body {
  font-family: "Lexend Deca", sans-serif;
  font-size: 1.5rem;
  color: #495567;
  line-height: 2.5rem;
  font-weight: 400;
  overflow-x: hidden;
  max-width: 144rem;
  margin: 0 auto;
}
